.header {
  text-align: center;
  background-color:blueviolet;
  height:10vh;
  width:100%;
  margin-top: 0%;
  
 
  
}



h1{
  text-align: center;
  color:chocolate;
}
h3{
  color: red;
}

ul {
  display: flex;
  list-style: none;
  justify-content: center;
  
  
 
}


li{
  margin-right: 20px;
  font-size: 25px;
  
  
}

.footer {
  text-align: center;
  background-color:blueviolet;
  height: 10vh;
  width:100%;
  margin-top: 35%;
  
}


button{
  display:inline-block;
  margin-left: 10px;
  font-size: 20px;
  text-decoration: none;
  border: none;
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
